.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container {
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    width: var(--default-layout-width);
    display: flex;
    padding: 0 24px;
    margin-top: 60px;
    position: relative;
}

.content {
    flex: 1;
    width: var(--default-layout-content-width);
    max-width: var(--default-layout-content-width);
    margin-left: 16px;
    padding: 24px 0;
    // margin-left: 356px;
    position: relative;
}
